@import 'ScssHelpers/helpers-all.scss';

.power {
	visibility: visible;
	background-color: $black;

	@include bp($point_2, min) {
		margin-bottom: 17rem;
		padding: 12.2rem var(--gl-indent) 11.6rem;
	}

	@include bp($point_3) {
		padding: 5rem var(--gl-indent) 3.6rem;
	}
}

.power__cols {
	display: flex;
	flex-wrap: wrap;
	margin-right: -2rem;
}

.power__col {
	@include bp($point_2, min) {
		width: 50%;
		padding-right: 2rem;
	}

	@include bp($point_3) {
		position: relative;
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 12.6rem;
		}
	}
}

.power__title {
	@include h2;

	position: relative;

	color: $white;

	h2 {
		font-weight: 400;
	}

	@include bp($point_2, min) {
		padding-bottom: 3rem;
	}

	@include bp($point_3) {
		padding-bottom: 1.8rem;
	}

	&:before {
		content: '';
		position: absolute;
		top: -1.4rem;
		left: -2rem;

		width: 1.5rem;
		height: 1.5rem;

		border-radius: 50%;
		background-color: currentColor;
	}

	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;

		width: 85%;
		height: 0.4rem;

		background-color: currentColor;

		@include bp($point_3) {
			height: 0.8rem;
		}
	}

	strong,
	span {
		font-weight: 400;
		color: $yellow;
	}
}

.power_gallery {
	position: relative;
	z-index: 2;

	@include bp($point_2, min) {
		width: 60.6rem;
		max-width: calc(100% + 3rem);
		margin: 9.4rem 0 -30rem 10.8rem;

		border: 0.4rem solid $black;
	}

	@include bp($point_3) {
		width: 26.5rem;
		margin: 0 auto;

		border-radius: 1rem;

		transform: translateX(-1.2rem);
		overflow: hidden;
	}

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		left: 1.2rem;
		top: 1.2rem;

		background-color: $black;
		border-radius: 0.4rem;
		filter: blur(0.4rem);
	}

	&:before {
		content: '';
		display: block;

		@include bp($point_2, min) {
			padding-bottom: 69.1%;
		}

		@include bp($point_3) {
			padding-bottom: 275.5%;
		}
	}
}

.power_gallery__list {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;

	width: 100%;
	height: 100%;

	overflow: hidden;
}

.power_gallery__item {
	position: absolute;
	top: calc(100% - 1px);
	left: 0;
	z-index: 2;

	width: 100%;
	height: calc(100% + 2px);

	&:nth-child(1) {
		top: -1px;
	}

	@include d3();
}

.power_gallery__pic {
	display: block;
	width: 100%;
	height: 100%;
}

.power_gallery__img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;

	@include d3();
}

.power_social_wrap {
	@include bp($point_2, min) {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		overflow: hidden;
	}
}

.power_social {
	position: absolute;
	left: 50%;

	@include bp($point_2, min) {
		top: 50%;

		width: 53rem;
		height: 53rem;

		margin-top: -13.2rem;
		margin-left: -4.6rem;
	}

	@include bp($point_3) {
		top: 0;

		width: 32.4rem;
		height: 32.4rem;

		margin: -6.7rem 0 0 -17.6rem;
	}
}

.power_social__img {
	display: block;
	width: 100%;
	height: 100%;

	overflow: visible;

	&.power_social__img--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}

	&.power_social__img--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.power_social__el {
	transform-origin: 50% 50%;

	will-change: transform;
}